@import "./config";

// Container
.container {
  max-width: $max-width;
  margin: auto;
  overflow: hidden;
  padding: 0 2rem;
  margin-top: 6rem;
  margin-bottom: 3rem;
}

// Text styles
.x-large {
  font-size: 4rem;
  line-height: 1.2;
  margin-bottom: 1rem;
}
.large {
  font-size: 3rem;
  line-height: 1.2;
  margin-bottom: 1rem;
}
.lead {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.text {
  &-primary {
    color: $primary-color;
  }
  &-dark {
    color: $dark-color;
  }
  &-danger {
    color: $danger-color;
  }
  &-success {
    color: $success-color;
  }
}
// Backgrounds

.bg {
  &-primary {
    @include set-background($primary-color);
  }
  &-light {
    @include set-background($light-color);
    border: #ccc 1px solid;
  }
  &-dark {
    @include set-background($dark-color);
  }
  &-success {
    @include set-background($success-color);
  }
  &-danger {
    @include set-background($danger-color);
  }
  &-white {
    @include set-background(#ffffff);
    border: #ccc 1px solid;
  }
}

// Buttons
.btn {
  display: inline-block;
  background: $light-color;
  color: #333333;
  padding: 0.4rem 1.3rem;
  cursor: pointer;
  font-size: 1rem;
  margin-right: 0.5rem;
  outline: none;
  border-radius: 0.3rem;
  border: none;
  transition: all 0.4s ease;
  &:hover {
    background: lighten($dark-color, 20%);
    color: #ffffff;
  }

  &.btn-primary {
    @include set-background($primary-color);
    &:hover {
      background: darken($primary-color, 5%);
    }
  }
  &.btn-dark {
    @include set-background($dark-color);
    &:hover {
      background: darken($dark-color, 5%);
    }
  }
  &.btn-danger {
    @include set-background($danger-color);
    &:hover {
      background: darken($danger-color, 5%);
    }
  }
  &.btn-success {
    @include set-background($success-color);
    &:hover {
      background: darken($success-color, 5%);
    }
  }
  &.btn-light {
    @include set-background($light-color);
    &:hover {
      background: darken($light-color, 10%);
    }
  }
}

// Dark Overlay
.dark-overlay {
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
}

// Image
.round-img {
  border-radius: 50%;
}

// Horizontal Line
.line {
  height: 1px;
  background: #ccc;
  margin: 1.5rem 0;
}

// ALERTS

.alert {
  padding: 0.8rem;
  margin: 1rem;
  opacity: 0.9;
  background: $light-color;
  border-radius: 0.3rem;

  &.alert-primary {
    @include set-background($primary-color);
  }
  &.alert-dark {
    @include set-background($dark-color);
  }
  &.alert-success {
    @include set-background($success-color);
  }
  &.alert-danger {
    @include set-background($danger-color);
  }
}

// Badges
.badge {
  font-size: 0.8rem;
  padding: 0.1rem;
  text-align: center;
  margin: 0.3rem;
  background: #333;
  &.badge-primary {
    @include set-background($primary-color);
  }
  &.badge-danger {
    @include set-background($danger-color);
  }
  &.badge-success {
    @include set-background($success-color);
  }
  &.badge-dark {
    @include set-background($dark-color);
  }
}
